import {Component, EventEmitter, Inject, Input, OnInit, Output} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {Variant} from '../../../model/variant.model';
import {Stock} from '../../../model/stock.model';
import {VariantService} from '../../../services/variant.service';

@Component({
  selector: 'app-variant-edit',
  templateUrl: './variant-edit.component.html',
  styleUrls: ['./variant-edit.component.css']
})
export class VariantEditComponent implements OnInit {

  variant: Variant;
  forms: FormGroup;
  isChecked: true;
  stock = Stock;
  stockKeys = Object.keys(this.stock);
  @Output()
  variantChange = new EventEmitter();

  constructor(private router: Router,
              private formBuilder: FormBuilder,
              private activatedRoute: ActivatedRoute,
              private variantService: VariantService,
              public dialogRef: MatDialogRef<VariantEditComponent>,
              @Inject(MAT_DIALOG_DATA) public data: Variant) { }

  ngOnInit(): void {
    this.initForm();
  }

  private initForm(): void {
    this.forms = this.formBuilder.group({
      id: '',
      produitId: this.data.produitId,
      name: ['', Validators.required],
      prix: ['', Validators.required],
      prixKg: ['', Validators.required],
      stock: Stock,
      // imageUrlOnSelect: ['', Validators.required],
      // imageUrlNonSelect: ['', Validators.required],
    });
    if (this.data && this.data.id) {
      this.variantService.getVariant(this.data.produitId, this.data.id).subscribe(data => {
        this.variant = data;
        this.forms.patchValue({
          id: data.id,
          produitId: data.produitId,
          name: data.name,
          prix: data.prix,
          prixKg: data.prixKg,
          stock: data.stock,
          // imageUrlOnSelect: data.imageUrlOnSelect,
          // imageUrlNonSelect: data.imageUrlNonSelect,
        });
      });
    }
  }

  onSubmit(): void {
    if (this.forms.invalid) {
      this.forms.markAllAsTouched();
      return;
    }
    if (!this.variant || this.variant.id == null) {
      this.variantService.createVariant(this.forms).subscribe(
        next => {
          this.variantChange.emit(next);
          this.dialogRef.close();
        });
    } else {
      this.variantService.updateVariant(this.forms).subscribe(
        next => {
          this.variantChange.emit(next);
        });
    }
  }

  updateStock(stock: any): void {
    this.forms.patchValue({
      stock
    });
  }

}
