import {AfterViewInit, Component, OnInit, ViewChild} from '@angular/core';

import {MatTableDataSource} from '@angular/material/table';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {Router} from '@angular/router';
import {MatDialog} from '@angular/material/dialog';
import {merge} from 'rxjs';
import {startWith, switchMap} from 'rxjs/operators';
import {SousCategorie} from '../../../model/sous-categorie';
import {SousCategorieService} from '../../../services/sous-categorie.service';
import {SousCategorieEditComponent} from '../../Categorie/sous-categorie-edit/sous-categorie-edit.component';

@Component({
  selector: 'app-admin-view-sous-categorie-list',
  templateUrl: './admin-view-sous-categorie-list.component.html',
  styleUrls: ['./admin-view-sous-categorie-list.component.css']
})
export class AdminViewSousCategorieListComponent implements AfterViewInit {

  displayedColumns: string[] = ['name', 'modifier', 'supprimer'];
  isLoadingResults = true;
  sousCategories: Array<SousCategorie>;
  dataSource = new MatTableDataSource<SousCategorie>();

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;


  constructor(private router: Router,
              private sousCategorieService: SousCategorieService,
              public dialog: MatDialog) {}

  ngAfterViewInit(): void {
    this.initSousCategories();
    this.sort.sortChange.subscribe(() => this.paginator.pageIndex = 0);
    merge(this.sort.sortChange, this.paginator.page).pipe(
      startWith({}),
      switchMap(() => {
        return this.sousCategorieService.getSousCategoriePage(this.paginator.pageIndex + 1, this.paginator.pageSize , this.sort.direction);
      }));
  }

  private initSousCategories(): void {
    this.sousCategorieService.getSousCategoriePage(0, 30, 'DESC').subscribe(data => {
        this.sousCategories = data.content;
        this.dataSource = new MatTableDataSource<SousCategorie>(this.sousCategories);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        console.log('data : ', data);
      },
      err => {
        console.log('error: ', err.error.message);
      });
  }

  creerSousCategorie(): void {
    const dialogRef = this.dialog.open(SousCategorieEditComponent, {
      width: '400px',
      height: '300px',
      data: {}
    });
    dialogRef.afterClosed().subscribe(next => {
      this.initSousCategories();
    });
  }

  modifierSousCategorie(cat: SousCategorie): void {
    const dialogRef = this.dialog.open(SousCategorieEditComponent, {
      width: '400px',
      height: '300px',
      data: cat
    });
    dialogRef.componentInstance.sousCategorieChange.subscribe(data => {
      this.initSousCategories();
    });
  }

  supprimerSousCategorie(id: number): void {
    this.sousCategorieService.deleteSousCategorie(id).subscribe(next => this.initSousCategories());
  }
}
